const API_BASE_URL = process.env.REACT_APP_API_URL;

class ApiService {
  async request(endpoint, options = {}) {
    const url = `${API_BASE_URL}${endpoint}`;
    const config = {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      ...options,
    };

    if (config.body && typeof config.body === 'object') {
      config.body = JSON.stringify(config.body);
    }

    const response = await fetch(url, config);
    
    if (!response.ok) {
      const error = await response.json().catch(() => ({ error: 'Request failed' }));
      throw new Error(error.error || 'Request failed');
    }

    return response.json();
  }

  // Auth
  async validateAdminCredentials(email, password) {
    return this.request('/auth', {
      method: 'POST',
      body: { email, password }
    });
  }

  // Users
  async listUsers(username = '') {
    const query = username ? `?username=${encodeURIComponent(username)}` : '';
    return this.request(`/users${query}`);
  }

  async deleteUser(username) {
    return this.request('/users/delete', {
      method: 'POST',
      body: { username }
    });
  }

  async resetUserPassword(username) {
    return this.request('/users/reset-password', {
      method: 'POST',
      body: { username }
    });
  }

  async setUserMFAPreference(username, enabled) {
    return this.request('/users/mfa', {
      method: 'POST',
      body: { username, enabled }
    });
  }

  // Pool configuration
  async getUserPoolMFAConfig() {
    return this.request('/pool/mfa');
  }

  async setUserPoolMFAConfig(mfaConfiguration) {
    return this.request('/pool/mfa', {
      method: 'POST',
      body: { mfaConfiguration }
    });
  }
}

export default new ApiService();