import { CognitoIdentityProviderClient, ListUsersCommand, AdminSetUserPasswordCommand, AdminInitiateAuthCommand, AdminRespondToAuthChallengeCommand, AdminCreateUserCommand, AdminUpdateUserAttributesCommand, AdminSetUserMFAPreferenceCommand, ListUserPoolClientsCommand, AssociateSoftwareTokenCommand, VerifySoftwareTokenCommand, AdminResetUserPasswordCommand, DescribeUserPoolCommand, AdminGetUserCommand, SetUserPoolMfaConfigCommand, AdminDeleteUserCommand } from '@aws-sdk/client-cognito-identity-provider';
import { DynamoDBClient } from '@aws-sdk/client-dynamodb';
import { DynamoDBDocumentClient, GetCommand } from '@aws-sdk/lib-dynamodb';
const USER_POOL_ID = process.env.REACT_APP_USER_POOL_ID || 'us-east-2_ioMTCkVZd';
const REGION = process.env.REACT_APP_REGION || 'us-east-2';
const CLIENT_SECRET = process.env.REACT_APP_CLIENT_SECRET || 'your-client-secret';
const ADMIN_TABLE_NAME = process.env.REACT_APP_ADMIN_TABLE_NAME || 'admin-credentials';

const calculateSecretHash = async (username, clientId, clientSecret) => {
  const message = username + clientId;
  const key = await crypto.subtle.importKey(
    'raw',
    new TextEncoder().encode(clientSecret),
    { name: 'HMAC', hash: 'SHA-256' },
    false,
    ['sign']
  );
  const signature = await crypto.subtle.sign('HMAC', key, new TextEncoder().encode(message));
  return btoa(String.fromCharCode(...new Uint8Array(signature)));
};

const client = new CognitoIdentityProviderClient({
  region: REGION,
  credentials: {
    accessKeyId: process.env.REACT_APP_AWS_ACCESS_KEY_ID,
    secretAccessKey: process.env.REACT_APP_AWS_SECRET_ACCESS_KEY,
    sessionToken: process.env.REACT_APP_AWS_SESSION_TOKEN
  }
});

// DynamoDB client for admin authentication
const dynamoClient = new DynamoDBClient({
  region: REGION,
  credentials: {
    accessKeyId: process.env.REACT_APP_AWS_ACCESS_KEY_ID,
    secretAccessKey: process.env.REACT_APP_AWS_SECRET_ACCESS_KEY,
    sessionToken: process.env.REACT_APP_AWS_SESSION_TOKEN
  }
});
const docClient = DynamoDBDocumentClient.from(dynamoClient);

export const listUsers = async (searchTerm = null) => {
  const params = {
    UserPoolId: USER_POOL_ID,
    Limit: 60
  };
  
  if (searchTerm) {
    // Try to search by email first if it looks like an email
    if (searchTerm.includes('@')) {
      params.Filter = `email = "${searchTerm}"`;
    } else {
      params.Filter = `username = "${searchTerm}"`;
    }
  }
  
  const command = new ListUsersCommand(params);
  const response = await client.send(command);
  return response.Users;
};



export const setPermanentPassword = async (username, newPassword) => {
  const CLIENT_ID = process.env.REACT_APP_CLIENT_ID;
  const secretHash = await calculateSecretHash(username, CLIENT_ID, CLIENT_SECRET);
  
  // First set a temporary password
  const tempPassword = 'TempSetup123!';
  const setTempParams = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    Password: tempPassword,
    Temporary: true
  };
  
  const tempCommand = new AdminSetUserPasswordCommand(setTempParams);
  await client.send(tempCommand);
  
  // Now authenticate with temp password to trigger NEW_PASSWORD_REQUIRED
  const authParams = {
    UserPoolId: USER_POOL_ID,
    ClientId: CLIENT_ID,
    AuthFlow: 'ADMIN_USER_PASSWORD_AUTH',
    AuthParameters: {
      USERNAME: username,
      PASSWORD: tempPassword,
      SECRET_HASH: secretHash
    }
  };
  
  const authCommand = new AdminInitiateAuthCommand(authParams);
  const authResponse = await client.send(authCommand);
  
  if (authResponse.ChallengeName === 'NEW_PASSWORD_REQUIRED') {
    // Complete the password change challenge with permanent password
    const challengeParams = {
      UserPoolId: USER_POOL_ID,
      ClientId: CLIENT_ID,
      ChallengeName: 'NEW_PASSWORD_REQUIRED',
      ChallengeResponses: {
        USERNAME: username,
        NEW_PASSWORD: newPassword,
        SECRET_HASH: secretHash
      },
      Session: authResponse.Session
    };
    
    const challengeCommand = new AdminRespondToAuthChallengeCommand(challengeParams);
    await client.send(challengeCommand);
  }
};

export const createUser = async (email, tempPassword) => {
  const params = {
    UserPoolId: USER_POOL_ID,
    Username: email,
    UserAttributes: [
      {
        Name: 'email',
        Value: email
      },
      {
        Name: 'email_verified',
        Value: 'true'
      }
    ],
    TemporaryPassword: tempPassword,
    MessageAction: 'SUPPRESS' // Don't send welcome email
  };
  
  const command = new AdminCreateUserCommand(params);
  const response = await client.send(command);
  
  return {
    username: response.User.Username,
    userSub: response.User.Attributes.find(attr => attr.Name === 'sub')?.Value
  };
};

export const setUserAttributes = async (username, attributes) => {
  const userAttributes = Object.entries(attributes).map(([name, value]) => ({
    Name: name,
    Value: value
  }));
  
  const params = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    UserAttributes: userAttributes
  };
  
  const command = new AdminUpdateUserAttributesCommand(params);
  await client.send(command);
};

export const deleteUser = async (username) => {
  const params = {
    UserPoolId: USER_POOL_ID,
    Username: username
  };
  
  const command = new AdminDeleteUserCommand(params);
  await client.send(command);
};

export const authenticateUser = async (username, password) => {
  const CLIENT_ID = process.env.REACT_APP_CLIENT_ID;
  const secretHash = await calculateSecretHash(username, CLIENT_ID, CLIENT_SECRET);
  
  const authParams = {
    UserPoolId: USER_POOL_ID,
    ClientId: CLIENT_ID,
    AuthFlow: 'ADMIN_USER_PASSWORD_AUTH',
    AuthParameters: {
      USERNAME: username,
      PASSWORD: password,
      SECRET_HASH: secretHash
    }
  };
  
  const command = new AdminInitiateAuthCommand(authParams);
  const response = await client.send(command);
  
  return response;
};

export const forcePasswordReset = async (username, newPassword = 'TempPass123!') => {
  const setPasswordParams = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    Password: newPassword,
    Temporary: true // Temporary password - user must change on next login
  };
  
  const command = new AdminSetUserPasswordCommand(setPasswordParams);
  await client.send(command);
};

export const resetTOTPMFA = async (username) => {
  const CLIENT_ID = process.env.REACT_APP_CLIENT_ID;
  const secretHash = await calculateSecretHash(username, CLIENT_ID, CLIENT_SECRET);
  const tempPassword = 'TempReset123!';
  
  // Step 1: Disable MFA completely
  const disableMFAParams = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    SoftwareTokenMfaSettings: {
      Enabled: false
    },
    SMSMfaSettings: {
      Enabled: false
    }
  };
  
  const disableMFACommand = new AdminSetUserMFAPreferenceCommand(disableMFAParams);
  await client.send(disableMFACommand);
  
  // Step 2: Set permanent password
  const setPasswordParams = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    Password: tempPassword,
    Permanent: true
  };
  
  const setPasswordCommand = new AdminSetUserPasswordCommand(setPasswordParams);
  await client.send(setPasswordCommand);
  
  // Step 3: Authenticate to get access token
  const authParams = {
    UserPoolId: USER_POOL_ID,
    ClientId: CLIENT_ID,
    AuthFlow: 'ADMIN_NO_SRP_AUTH',
    AuthParameters: {
      USERNAME: username,
      PASSWORD: tempPassword,
      SECRET_HASH: secretHash
    }
  };
  
  const authCommand = new AdminInitiateAuthCommand(authParams);
  const authResponse = await client.send(authCommand);
  
  if (!authResponse.AuthenticationResult?.AccessToken) {
    throw new Error('Failed to get access token for MFA reset');
  }
  
  const accessToken = authResponse.AuthenticationResult.AccessToken;
  
  // Step 4: Associate new software token
  const associateTokenParams = {
    AccessToken: accessToken
  };
  
  const associateCommand = new AssociateSoftwareTokenCommand(associateTokenParams);
  const associateResponse = await client.send(associateCommand);
  
  return {
    secretCode: associateResponse.SecretCode,
    accessToken,
    tempPassword
  };
};

export const completeTOTPSetup = async (accessToken, totpCode, username) => {
  // Step 1: Verify the TOTP code
  const verifyParams = {
    AccessToken: accessToken,
    UserCode: totpCode
  };
  
  const verifyCommand = new VerifySoftwareTokenCommand(verifyParams);
  const verifyResponse = await client.send(verifyCommand);
  
  if (verifyResponse.Status !== 'SUCCESS') {
    throw new Error('TOTP verification failed');
  }
  
  // Step 2: Enable TOTP MFA for the user
  const enableMFAParams = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    SoftwareTokenMfaSettings: {
      Enabled: true,
      PreferredMfa: true
    }
  };
  
  const enableMFACommand = new AdminSetUserMFAPreferenceCommand(enableMFAParams);
  await client.send(enableMFACommand);
  
  // Record admin's decision to enable MFA
  await setAdminMFAPreference(username, 'admin_enabled');
  
  // Step 3: Force password reset so user must change password on next login
  const resetParams = {
    UserPoolId: USER_POOL_ID,
    Username: username
  };
  
  const resetCommand = new AdminResetUserPasswordCommand(resetParams);
  await client.send(resetCommand);
  
  return verifyResponse;
};

export const enableTOTPMFA = async (username) => {
  // Enable TOTP MFA for the user
  const enableMFAParams = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    SoftwareTokenMfaSettings: {
      Enabled: true,
      PreferredMfa: true
    }
  };
  
  const enableMFACommand = new AdminSetUserMFAPreferenceCommand(enableMFAParams);
  await client.send(enableMFACommand);
  
  // Record admin's decision to enable MFA
  await setAdminMFAPreference(username, 'admin_enabled');
};

export const finalizePasswordReset = async (username) => {
  // Force password reset so user must change password on next login
  const resetParams = {
    UserPoolId: USER_POOL_ID,
    Username: username
  };
  
  const resetCommand = new AdminResetUserPasswordCommand(resetParams);
  await client.send(resetCommand);
};

export const enableMFAForUser = async (username, newPassword = 'TempMFA123!') => {
  const CLIENT_ID = process.env.REACT_APP_CLIENT_ID;
  const secretHash = await calculateSecretHash(username, CLIENT_ID, CLIENT_SECRET);
  
  // Set permanent password
  const setPasswordParams = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    Password: newPassword,
    Permanent: true
  };
  
  const setPasswordCommand = new AdminSetUserPasswordCommand(setPasswordParams);
  await client.send(setPasswordCommand);
  
  // Authenticate to get access token
  const authParams = {
    UserPoolId: USER_POOL_ID,
    ClientId: CLIENT_ID,
    AuthFlow: 'ADMIN_USER_PASSWORD_AUTH',
    AuthParameters: {
      USERNAME: username,
      PASSWORD: newPassword,
      SECRET_HASH: secretHash
    }
  };
  
  const authCommand = new AdminInitiateAuthCommand(authParams);
  const authResponse = await client.send(authCommand);
  
  let accessToken;
  
  if (authResponse.AuthenticationResult?.AccessToken) {
    accessToken = authResponse.AuthenticationResult.AccessToken;
  } else if (authResponse.ChallengeName === 'NEW_PASSWORD_REQUIRED') {
    // Handle new password challenge
    const challengeParams = {
      UserPoolId: USER_POOL_ID,
      ClientId: CLIENT_ID,
      ChallengeName: 'NEW_PASSWORD_REQUIRED',
      ChallengeResponses: {
        USERNAME: username,
        NEW_PASSWORD: newPassword,
        SECRET_HASH: secretHash
      },
      Session: authResponse.Session
    };
    
    const challengeCommand = new AdminRespondToAuthChallengeCommand(challengeParams);
    const challengeResponse = await client.send(challengeCommand);
    
    if (!challengeResponse.AuthenticationResult?.AccessToken) {
      throw new Error('Failed to get access token after password challenge');
    }
    
    accessToken = challengeResponse.AuthenticationResult.AccessToken;
  } else {
    throw new Error(`Authentication failed: ${authResponse.ChallengeName || 'Unknown error'}`);
  }
  
  // Associate software token
  const associateTokenParams = {
    AccessToken: accessToken
  };
  
  const associateCommand = new AssociateSoftwareTokenCommand(associateTokenParams);
  const associateResponse = await client.send(associateCommand);
  
  return {
    secretCode: associateResponse.SecretCode,
    accessToken,
    tempPassword: newPassword
  };
};

export const disableTOTPMFA = async (username) => {
  // Record admin's decision to disable MFA BEFORE disabling (to preserve history)
  await setAdminMFAPreference(username, 'admin_disabled');
  
  const disableMFAParams = {
    UserPoolId: USER_POOL_ID,
    Username: username,
    SoftwareTokenMfaSettings: {
      Enabled: false
    },
    SMSMfaSettings: {
      Enabled: false
    }
  };
  
  const disableMFACommand = new AdminSetUserMFAPreferenceCommand(disableMFAParams);
  await client.send(disableMFACommand);
};

export const getUserPoolMFAConfig = async () => {
  const params = {
    UserPoolId: USER_POOL_ID
  };
  
  const command = new DescribeUserPoolCommand(params);
  const response = await client.send(command);
  
  return {
    mfaConfiguration: response.UserPool.MfaConfiguration, // 'OFF', 'ON', 'OPTIONAL'
    enabledMfaMethods: response.UserPool.EnabledMfas || []
  };
};

export const getUserMFAStatus = async (username, userPoolMFAConfig = null) => {
  // If MFA is REQUIRED at pool level, all users are considered MFA Active
  if (userPoolMFAConfig && userPoolMFAConfig.mfaConfiguration === 'ON') {
    return {
      mfaActive: true,
      totpEnabled: true,
      smsEnabled: false
    };
  }
  
  const params = {
    UserPoolId: USER_POOL_ID,
    Username: username
  };
  
  const command = new AdminGetUserCommand(params);
  const response = await client.send(command);
  
  // Check UserMFASettingList for current MFA preferences
  const mfaSettings = response.UserMFASettingList || [];
  const totpEnabled = mfaSettings.includes('SOFTWARE_TOKEN_MFA');
  const smsEnabled = mfaSettings.includes('SMS_MFA');
  
  // Also check MFAOptions as fallback
  const mfaOptions = response.MFAOptions || [];
  const hasTOTPOption = mfaOptions.some(option => option.DeliveryMedium === 'SOFTWARE_TOKEN_MFA');
  const hasSMSOption = mfaOptions.some(option => option.DeliveryMedium === 'SMS');
  
  return {
    mfaActive: totpEnabled || smsEnabled || mfaOptions.length > 0,
    totpEnabled: totpEnabled || hasTOTPOption,
    smsEnabled: smsEnabled || hasSMSOption
  };
};

// Check if user has MFA registration history (previously registered TOTP)
export const checkUserMFAHistory = async (username) => {
  try {
    const params = {
      UserPoolId: USER_POOL_ID,
      Username: username
    };
    
    const command = new AdminGetUserCommand(params);
    const response = await client.send(command);
    
    // Check multiple indicators of MFA history:
    // 1. MFAOptions (current MFA methods)
    const mfaOptions = response.MFAOptions || [];
    
    // 2. UserMFASettingList (MFA preferences)
    const mfaSettings = response.UserMFASettingList || [];
    
    // 3. Check admin preference in localStorage (if admin previously managed this user's MFA)
    const adminPreference = await getAdminMFAPreference(username);
    const hasAdminMFAHistory = adminPreference === 'admin_enabled' || adminPreference === 'admin_disabled';
    
    // 4. Check user attributes for any MFA-related attributes
    const userAttributes = response.UserAttributes || [];
    const hasMFAAttributes = userAttributes.some(attr => 
      attr.Name.includes('mfa') || 
      attr.Name.includes('phone') || 
      attr.Name === 'software_token_mfa_enabled'
    );
    
    // User has MFA history if ANY of these conditions are true:
    const hasMFAHistory = 
      mfaOptions.length > 0 || 
      mfaSettings.length > 0 || 
      hasAdminMFAHistory || 
      hasMFAAttributes;
    
    console.log(`MFA History check for ${username}:`, {
      mfaOptions: mfaOptions.length,
      mfaSettings: mfaSettings.length,
      adminPreference,
      hasMFAAttributes,
      result: hasMFAHistory
    });
    
    return hasMFAHistory;
  } catch (error) {
    console.error('Error checking MFA history:', error);
    return false;
  }
};

export const enableMFAForAllUsers = async () => {
  try {
    // Get all users
    const users = await listUsers();
    
    // Enable MFA for each user
    const enablePromises = users.map(async (user) => {
      try {
        await enableTOTPMFA(user.Username);
      } catch (error) {
        console.log(`Could not enable MFA for user ${user.Username}:`, error.message);
      }
    });
    
    await Promise.all(enablePromises);
  } catch (error) {
    console.error('Error enabling MFA for all users:', error);
    throw error;
  }
};

// Set admin MFA preference for a user using localStorage
export const setAdminMFAPreference = async (username, preference) => {
  try {
    const preferences = JSON.parse(localStorage.getItem('adminMFAPreferences') || '{}');
    preferences[username] = preference;
    localStorage.setItem('adminMFAPreferences', JSON.stringify(preferences));
  } catch (error) {
    console.error('Error saving admin MFA preference:', error);
  }
};

// Get admin MFA preference for a user from localStorage
export const getAdminMFAPreference = async (username) => {
  try {
    const preferences = JSON.parse(localStorage.getItem('adminMFAPreferences') || '{}');
    return preferences[username] || 'not_set';
  } catch (error) {
    console.error('Error getting admin MFA preference:', error);
    return 'not_set';
  }
};

// Restore MFA settings based on admin preferences
export const restoreMFAFromAdminPreferences = async () => {
  try {
    const users = await listUsers();
    
    for (const user of users) {
      try {
        const adminPreference = await getAdminMFAPreference(user.Username);
        const currentMFAStatus = await getUserMFAStatus(user.Username);
        
        // Only act on users with explicit admin preferences
        if (adminPreference === 'admin_disabled' && currentMFAStatus.mfaActive) {
          await disableTOTPMFA(user.Username);
          console.log(`Restored MFA disabled for ${user.Username} based on admin preference`);
        } else if (adminPreference === 'admin_enabled' && !currentMFAStatus.mfaActive) {
          // Only try to enable if user has MFA capability
          try {
            await enableTOTPMFA(user.Username);
            console.log(`Restored MFA enabled for ${user.Username} based on admin preference`);
          } catch (error) {
            console.log(`Could not restore MFA for ${user.Username}: ${error.message}`);
          }
        }
      } catch (error) {
        console.log(`Error processing MFA preference for ${user.Username}:`, error.message);
      }
    }
  } catch (error) {
    console.error('Error restoring MFA from admin preferences:', error);
  }
};

export const updateUserPoolMFAConfig = async (mfaConfiguration) => {
  const params = {
    UserPoolId: USER_POOL_ID,
    MfaConfiguration: mfaConfiguration, // 'OFF', 'ON', 'OPTIONAL'
    SoftwareTokenMfaConfiguration: {
      Enabled: true
    }
  };
  
  const command = new SetUserPoolMfaConfigCommand(params);
  await client.send(command);
  
  // If switching to OPTIONAL, restore MFA based on admin preferences instead of enabling for all
  if (mfaConfiguration === 'OPTIONAL') {
    setTimeout(async () => {
      try {
        await restoreMFAFromAdminPreferences();
        console.log('MFA settings restored based on admin preferences');
      } catch (error) {
        console.error('Failed to restore MFA settings:', error);
      }
    }, 1000);
  }
};

// Admin authentication using DynamoDB
export const validateAdminCredentials = async (email, password) => {
  try {
    const params = {
      TableName: ADMIN_TABLE_NAME,
      Key: { email: email }
    };

    const command = new GetCommand(params);
    const response = await docClient.send(command);

    if (!response.Item) {
      return { success: false, message: 'Admin not found' };
    }

    if (response.Item.password === password) {
      return { 
        success: true, 
        admin: {
          email: response.Item.email,
          name: response.Item.name || 'Admin'
        }
      };
    } else {
      return { success: false, message: 'Invalid password' };
    }

  } catch (error) {
    console.error('DynamoDB authentication error:', error);
    return { success: false, message: 'Authentication service error' };
  }
};

