import React, { useState } from 'react';
import { createUser, setUserAttributes, setPermanentPassword } from './cognitoService';
import './CreateUserPage.css';

function CreateUserPage({ onBackToLanding }) {
  const [step, setStep] = useState(1);
  const [userData, setUserData] = useState({
    email: '',
    tempPassword: '',
    username: '',
    company: '',
    name: ''
  });
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  const handleStep1Submit = async (e) => {
    e.preventDefault();
    if (!userData.email || !userData.tempPassword) {
      setMessage('Please fill in all fields');
      return;
    }

    setLoading(true);
    try {
      const result = await createUser(userData.email, userData.tempPassword);
      setUserData(prev => ({ ...prev, username: result.username }));
      setMessage('User created successfully!');
      setStep(2);
    } catch (error) {
      setMessage(`Error creating user: ${error.message}`);
    }
    setLoading(false);
  };

  const handleStep2Submit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      await setUserAttributes(userData.username, {
        'profile': userData.company,
        'name': userData.name
      });
      setMessage('User attributes set successfully!');
      setStep(3);
    } catch (error) {
      setMessage(`Error setting attributes: ${error.message}`);
    }
    setLoading(false);
  };

  const handleSetPermanentPassword = async () => {
    const newPassword = prompt('Enter permanent password (min 8 chars, uppercase, lowercase, number):');
    if (!newPassword) return;
    
    setLoading(true);
    try {
      await setPermanentPassword(userData.username, newPassword);
      setMessage('Permanent password set successfully!');
    } catch (error) {
      setMessage(`Error setting permanent password: ${error.message}`);
    }
    setLoading(false);
  };

  const resetForm = () => {
    setStep(1);
    setUserData({ email: '', tempPassword: '', username: '', company: '', name: '' });
    setMessage('');
  };

  return (
    <div className="create-user-page">
      <div className="header">
        <h1>Create New User</h1>
      </div>

      <div className="progress-bar">
        <div className={`step ${step >= 1 ? 'active' : ''}`}>1. User Details</div>
        <div className={`step ${step >= 2 ? 'active' : ''}`}>2. Attributes</div>
        <div className={`step ${step >= 3 ? 'active' : ''}`}>3. Complete</div>
      </div>

      {message && <div className="message">{message}</div>}

      {step === 1 && (
        <div className="step-content">
          <h2>Step 1: User Details</h2>
          <form onSubmit={handleStep1Submit}>
            <div className="form-group">
              <label>Email Address:</label>
              <input
                type="email"
                value={userData.email}
                onChange={(e) => setUserData(prev => ({ ...prev, email: e.target.value }))}
                required
              />
            </div>
            <div className="form-group">
              <label>Temporary Password:</label>
              <input
                type="password"
                value={userData.tempPassword}
                onChange={(e) => setUserData(prev => ({ ...prev, tempPassword: e.target.value }))}
                placeholder="Min 8 chars, uppercase, lowercase, number"
                required
              />
            </div>
            <button type="submit" disabled={loading} className="create-btn">
              {loading ? 'Creating User...' : 'Create User'}
            </button>
          </form>
        </div>
      )}

      {step === 2 && (
        <div className="step-content">
          <h2>Step 2: Set User Attributes</h2>
          <form onSubmit={handleStep2Submit}>
            <div className="form-group">
              <label>Company (profile):</label>
              <input
                type="text"
                value={userData.company}
                onChange={(e) => setUserData(prev => ({ ...prev, company: e.target.value }))}
                placeholder="Enter company name"
                required
              />
            </div>
            <div className="form-group">
              <label>Full Name:</label>
              <input
                type="text"
                value={userData.name}
                onChange={(e) => setUserData(prev => ({ ...prev, name: e.target.value }))}
                placeholder="Enter full name"
                required
              />
            </div>
            <div className="attributes-info">
              <p><strong>Email:</strong> {userData.email}</p>
            </div>
            <button type="submit" disabled={loading} className="continue-btn">
              {loading ? 'Setting Attributes...' : 'Set Attributes & Continue'}
            </button>
          </form>
        </div>
      )}

      {step === 3 && (
        <div className="step-content">
          <h2>Step 3: User Creation Complete!</h2>
          <div className="success-info">
            <p>• User created successfully</p>
            <p>• Attributes configured</p>
            <div className="user-info-container">
              <div className="user-info-content">
                <p><strong>Username:</strong> {userData.email}</p>
                <p><strong>Temporary Password:</strong> {userData.tempPassword}</p>
                <p><strong>Company:</strong> {userData.company}</p>
                <p><strong>Full Name:</strong> {userData.name}</p>
              </div>
              <button 
                onClick={() => navigator.clipboard.writeText(`Username: ${userData.email}\nTemporary Password: ${userData.tempPassword}\nCompany: ${userData.company}\nFull Name: ${userData.name}`)}
                className="copy-btn"
              >
                Copy
              </button>
            </div>
          </div>
          <div className="optional-actions">
            <button 
              onClick={handleSetPermanentPassword} 
              disabled={loading}
              className="set-password-btn"
            >
              {loading ? 'Setting Password...' : 'Set Permanent Password (Optional)'}
            </button>
          </div>
          <button onClick={resetForm} className="create-another-btn">
            Create Another User
          </button>
        </div>
      )}
    </div>
  );
}

export default CreateUserPage;