import React, { useState, useEffect } from 'react';
import apiService from './apiService';
import './AdminPage.css';

function AdminPage({ onBackToLanding }) {
  const [users, setUsers] = useState([]);
  const [searchUsername, setSearchUsername] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [resetPassword, setResetPassword] = useState('');
  const [totpResetData, setTotpResetData] = useState(null);
  const [totpCode, setTotpCode] = useState('');
  const [totpVerified, setTotpVerified] = useState(false);
  const [userPoolMFAConfig, setUserPoolMFAConfig] = useState(null);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [confirmModalData, setConfirmModalData] = useState(null);
  const [showInputModal, setShowInputModal] = useState(false);
  const [inputModalData, setInputModalData] = useState(null);
  const [inputValue, setInputValue] = useState('');

  const loadUsers = async (username = null, mfaConfig = null) => {
    setLoading(true);
    try {
      const userList = await apiService.listUsers(username);
      const currentMfaConfig = mfaConfig || userPoolMFAConfig;
      
      // Get MFA status for each user only when MFA Configuration is OPTIONAL
      const usersWithMFA = await Promise.all(
        userList.map(async (user) => {
          try {
            // Only get MFA status when configuration is OPTIONAL
            if (currentMfaConfig && currentMfaConfig.mfaConfiguration === 'OPTIONAL') {
              const mfaStatus = await getUserMFAStatus(user.Username, currentMfaConfig);
              return { ...user, mfaStatus };
            }
            
            // For REQUIRED or other configurations, don't fetch MFA status
            return { ...user, mfaStatus: { mfaActive: false } };
          } catch (error) {
            return { ...user, mfaStatus: { mfaActive: false } };
          }
        })
      );
      
      setUsers(usersWithMFA);
      setMessage('');
    } catch (error) {
      setMessage(`Error: ${error.message}`);
    }
    setLoading(false);
  };

  const refreshData = async (username = null) => {
    try {
      const mfaConfig = await apiService.getUserPoolMFAConfig();
      setUserPoolMFAConfig(mfaConfig);
      await loadUsers(username, mfaConfig);
    } catch (error) {
      console.error('Error refreshing data:', error);
    }
  };

  const handleSearch = () => {
    loadUsers(searchUsername || null);
  };



  const handleSetPermanentPassword = (username, userEmail) => {
    setInputModalData({
      title: 'Set Permanent Password',
      message: `Enter permanent password for ${userEmail}:`,
      placeholder: 'Min 8 chars, uppercase, lowercase, number',
      onConfirm: async (password) => {
        if (!password) return;
        try {
          await setPermanentPassword(username, password);
          setMessage(`Permanent password set for ${userEmail}.`);
          loadUsers(searchUsername || null);
        } catch (error) {
          setMessage(`Error setting permanent password: ${error.message}`);
        }
        setShowInputModal(false);
        setInputValue('');
      },
      onCancel: () => {
        setShowInputModal(false);
        setInputValue('');
      }
    });
    setShowInputModal(true);
  };

  const handleForcePasswordReset = (username, userEmail) => {
    setInputModalData({
      title: 'Force Password Reset',
      message: `Enter new temporary password for ${userEmail}:`,
      placeholder: 'Min 8 chars, uppercase, lowercase, number',
      onConfirm: async (password) => {
        if (!password) return;
        try {
          await forcePasswordReset(username, password);
          setMessage(`Password reset to '${password}' for ${userEmail}. User must change on next login.`);
        } catch (error) {
          setMessage(`Error forcing password reset: ${error.message}`);
        }
        setShowInputModal(false);
        setInputValue('');
      },
      onCancel: () => {
        setShowInputModal(false);
        setInputValue('');
      }
    });
    setShowInputModal(true);
  };

  const handleResetTOTP = (username, userEmail) => {
    if (!userPoolMFAConfig || userPoolMFAConfig.mfaConfiguration !== 'OPTIONAL') {
      setMessage('Switch to MFA Configuration: OPTIONAL to use this feature.');
      return;
    }

    setConfirmModalData({
      title: 'Reset TOTP MFA',
      message: `This will reset TOTP MFA for ${userEmail}. The user will need to set up their authenticator app again. Continue?`,
      onConfirm: async () => {
        try {
          setMessage('Resetting TOTP MFA...');
          const resetData = await resetTOTPMFA(username);
          setTotpResetData({ ...resetData, username, userEmail });
          setMessage(`TOTP reset initiated for ${userEmail}. Please provide the secret code to the user.`);
        } catch (error) {
          setMessage(`Error resetting TOTP: ${error.message}`);
        }
        setShowConfirmModal(false);
      },
      onCancel: () => setShowConfirmModal(false)
    });
    setShowConfirmModal(true);
  };

  const handleCompleteTOTPSetup = async () => {
    if (!totpCode || !totpResetData) return;

    try {
      setMessage('Verifying TOTP code...');
      await completeTOTPSetup(totpResetData.accessToken, totpCode, totpResetData.username);
      
      setMessage(`TOTP MFA successfully verified for ${totpResetData.userEmail || totpResetData.username}.`);
      setTotpVerified(true);
      loadUsers(searchUsername || null);
    } catch (error) {
      setMessage(`Error completing TOTP setup: ${error.message}`);
    }
  };

  const handleForcePasswordResetFromTOTP = () => {
    const username = totpResetData.username;
    const userEmail = totpResetData.userEmail;
    
    setInputModalData({
      title: 'Force Password Reset',
      message: `Enter new temporary password for ${userEmail}:`,
      placeholder: 'Min 8 chars, uppercase, lowercase, number',
      onConfirm: async (password) => {
        if (!password) return;
        try {
          await forcePasswordReset(username, password);
          setMessage(`Password reset completed for ${userEmail}. User must change on next login.`);
          setTotpResetData(null);
          setTotpCode('');
          setTotpVerified(false);
          loadUsers(searchUsername || null);
        } catch (error) {
          setMessage(`Error forcing password reset: ${error.message}`);
        }
        setShowInputModal(false);
        setInputValue('');
      },
      onCancel: () => {
        setShowInputModal(false);
        setInputValue('');
      }
    });
    setShowInputModal(true);
  };

  const handleCancelTOTPReset = () => {
    const username = totpResetData.username;
    const userEmail = totpResetData.userEmail;
    
    // Clear TOTP reset data first
    setTotpResetData(null);
    setTotpCode('');
    setTotpVerified(false);
    
    // Immediately prompt for password reset since MFA reset corrupts password
    setInputModalData({
      title: 'Set Password After MFA Reset',
      message: `MFA reset cancelled. Set new password for ${userEmail} to allow login:`,
      placeholder: 'Min 8 chars, uppercase, lowercase, number',
      onConfirm: async (password) => {
        if (!password) return;
        try {
          await forcePasswordReset(username, password);
          setMessage(`Password set to '${password}' for ${userEmail}. User must change on next login.`);
          loadUsers(searchUsername || null);
        } catch (error) {
          setMessage(`Error setting password: ${error.message}`);
        }
        setShowInputModal(false);
        setInputValue('');
      },
      onCancel: () => {
        setShowInputModal(false);
        setInputValue('');
        setMessage('MFA reset cancelled. User may need password reset to login.');
        loadUsers(searchUsername || null);
      }
    });
    setShowInputModal(true);
  };

  const handleEnableMFA = async (username, userEmail) => {
    if (!userPoolMFAConfig || userPoolMFAConfig.mfaConfiguration !== 'OPTIONAL') {
      setMessage('Switch to MFA Configuration: OPTIONAL to use this feature.');
      return;
    }

    try {
      // Check if user has MFA registration history
      const hasMFAHistory = await checkUserMFAHistory(username);
      
      if (!hasMFAHistory) {
        setMessage(`Error: ${userEmail} has no MFA registration history. Please use 'Reset MFA' button to register MFA first.`);
        return;
      }

      setConfirmModalData({
        title: 'Enable MFA',
        message: `This will re-enable TOTP MFA for ${userEmail}. Continue?`,
        onConfirm: async () => {
          try {
            await enableTOTPMFA(username);
            setMessage(`TOTP MFA enabled for ${userEmail}.`);
            loadUsers(searchUsername || null);
          } catch (error) {
            setMessage(`Error enabling MFA: ${error.message}`);
          }
          setShowConfirmModal(false);
        },
        onCancel: () => setShowConfirmModal(false)
      });
      setShowConfirmModal(true);
    } catch (error) {
      setMessage(`Error checking MFA history: ${error.message}`);
    }
  };

  const handleDisableMFA = (username, userEmail) => {
    if (!userPoolMFAConfig || userPoolMFAConfig.mfaConfiguration !== 'OPTIONAL') {
      setMessage('Switch to MFA Configuration: OPTIONAL to use this feature.');
      return;
    }

    setConfirmModalData({
      title: 'Disable MFA',
      message: `This will disable TOTP MFA for ${userEmail}. They will no longer need MFA to login. Continue?`,
      onConfirm: async () => {
        try {
          await disableTOTPMFA(username);
          setMessage(`TOTP MFA disabled for ${userEmail}.`);
          setShowConfirmModal(false);
          
          // Immediately prompt for password reset since disabling MFA corrupts password
          setInputModalData({
            title: 'Set Password After MFA Disable',
            message: `MFA disabled. Set new password for ${userEmail} to allow login:`,
            placeholder: 'Min 8 chars, uppercase, lowercase, number',
            onConfirm: async (password) => {
              if (!password) return;
              try {
                await forcePasswordReset(username, password);
                setMessage(`MFA disabled and password set to '${password}' for ${userEmail}. User must change on next login.`);
                loadUsers(searchUsername || null);
              } catch (error) {
                setMessage(`Error setting password: ${error.message}`);
              }
              setShowInputModal(false);
              setInputValue('');
            },
            onCancel: () => {
              setShowInputModal(false);
              setInputValue('');
              setMessage('MFA disabled. User may need password reset to login.');
              loadUsers(searchUsername || null);
            }
          });
          setShowInputModal(true);
          
        } catch (error) {
          setMessage(`Error disabling MFA: ${error.message}`);
          setShowConfirmModal(false);
        }
      },
      onCancel: () => setShowConfirmModal(false)
    });
    setShowConfirmModal(true);
  };

  const handleTogglePoolMFA = () => {
    if (!userPoolMFAConfig) return;
    
    const currentConfig = userPoolMFAConfig.mfaConfiguration;
    const newConfig = currentConfig === 'ON' ? 'OPTIONAL' : 'ON';
    const displayCurrent = currentConfig === 'ON' ? 'REQUIRED' : currentConfig;
    const displayNew = newConfig === 'ON' ? 'REQUIRED' : newConfig;
    
    setConfirmModalData({
      title: 'Change MFA Configuration',
      message: `Change MFA configuration from ${displayCurrent} to ${displayNew}?`,
      onConfirm: async () => {
        try {
          setMessage('Updating MFA configuration...');
          await apiService.setUserPoolMFAConfig(newConfig);
          setMessage(`MFA configuration updated to ${displayNew}.`);
          refreshData(searchUsername || null);
        } catch (error) {
          setMessage(`Error updating MFA configuration: ${error.message}`);
        }
        setShowConfirmModal(false);
      },
      onCancel: () => setShowConfirmModal(false)
    });
    setShowConfirmModal(true);
  };

  const handleDeleteUser = (username, userEmail) => {
    setConfirmModalData({
      title: 'Delete User',
      message: `Are you sure you want to permanently delete user ${userEmail}? This action cannot be undone.`,
      onConfirm: async () => {
        try {
          await apiService.deleteUser(username);
          setMessage(`User ${userEmail} has been deleted successfully.`);
          loadUsers(searchUsername || null);
        } catch (error) {
          setMessage(`Error deleting user: ${error.message}`);
        }
        setShowConfirmModal(false);
      },
      onCancel: () => setShowConfirmModal(false)
    });
    setShowConfirmModal(true);
  };





  useEffect(() => {
    refreshData();
  }, []);

  return (
    <div className="admin-page">
      <div className="header">
        <h1>User Management</h1>
        {userPoolMFAConfig && (
          <div className={`mfa-config-display ${userPoolMFAConfig.mfaConfiguration.toLowerCase()}`}>
            <p>MFA Configuration: <strong>{userPoolMFAConfig.mfaConfiguration === 'ON' ? 'REQUIRED' : userPoolMFAConfig.mfaConfiguration}</strong> <span className="enabled-status">ENABLED</span></p>
            {(userPoolMFAConfig.mfaConfiguration === 'ON' || userPoolMFAConfig.mfaConfiguration === 'OPTIONAL') && (
              <button onClick={handleTogglePoolMFA} className="toggle-mfa-btn">
                Switch to MFA {userPoolMFAConfig.mfaConfiguration === 'ON' ? 'OPTIONAL' : 'REQUIRED'} Configuration
              </button>
            )}
          </div>
        )}
      </div>
      
      <div className="search-section">
        <input
          type="text"
          placeholder="Search by username"
          value={searchUsername}
          onChange={(e) => setSearchUsername(e.target.value)}
        />
        <button onClick={handleSearch} disabled={loading}>
          {loading ? 'Searching...' : 'Search'}
        </button>
        <button onClick={() => { setSearchUsername(''); refreshData(); }}>
          List All Users
        </button>
        <button onClick={() => { setSearchUsername(''); refreshData(); }} className="refresh-btn">
          Refresh
        </button>

      </div>

      {message && (
        <div className={`message ${message.includes('Switch to MFA Configuration: OPTIONAL') ? 'error-message' : ''}`}>
          {message}
        </div>
      )}

      {showConfirmModal && confirmModalData && (
        <div className="confirm-modal">
          <div className="confirm-modal-content">
            <h3>{confirmModalData.title}</h3>
            <p>{confirmModalData.message}</p>
            <div className="confirm-modal-buttons">
              <button onClick={confirmModalData.onCancel} className="cancel-btn">
                Cancel
              </button>
              <button onClick={confirmModalData.onConfirm} className="confirm-btn">
                OK
              </button>
            </div>
          </div>
        </div>
      )}

      {showInputModal && inputModalData && (
        <div className="confirm-modal">
          <div className="confirm-modal-content">
            <h3>{inputModalData.title}</h3>
            <p>{inputModalData.message}</p>
            <input
              type="password"
              placeholder={inputModalData.placeholder}
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              className="modal-input"
              autoFocus
            />
            <div className="confirm-modal-buttons">
              <button onClick={inputModalData.onCancel} className="cancel-btn">
                Cancel
              </button>
              <button 
                onClick={() => inputModalData.onConfirm(inputValue)} 
                className="confirm-btn"
                disabled={!inputValue}
              >
                OK
              </button>
            </div>
          </div>
        </div>
      )}

      {totpResetData && (
        <div className="totp-reset-modal">
          <div className="totp-reset-content">
            <h3>MFA Reset for {totpResetData.userEmail || totpResetData.username}</h3>
            <div className="totp-instructions">
              {!totpVerified ? (
                <>
                  <p><strong>Step 1:</strong> Provide this secret code to the user:</p>
                  <div className="secret-code-container">
                    <div className="secret-code-wrapper">
                      <code className="secret-code-text">{totpResetData.secretCode}</code>
                    </div>
                    <button 
                      onClick={() => navigator.clipboard.writeText(totpResetData.secretCode)}
                      className="copy-btn"
                    >
                      Copy
                    </button>
                  </div>
                  <p><strong>Step 2:</strong> User should add this code to their authenticator app manually</p>
                  <p><strong>Step 3:</strong> Enter the 6-digit TOTP code from the user's authenticator app:</p>
                  <div className="totp-verification">
                    <input
                      type="text"
                      placeholder="Enter 6-digit TOTP code"
                      value={totpCode}
                      onChange={(e) => setTotpCode(e.target.value)}
                      maxLength="6"
                    />
                    <button 
                      onClick={handleCompleteTOTPSetup}
                      disabled={totpCode.length !== 6}
                      className="verify-btn"
                    >
                      Verify
                    </button>
                    <button onClick={handleCancelTOTPReset} className="cancel-btn">
                      Cancel
                    </button>
                  </div>
                  <div className="totp-warning">
                    <p><strong>Note:</strong> You have 30 seconds to enter the TOTP code before it expires.</p>
                  </div>
                </>
              ) : (
                <>
                  <p className="success-message"><strong>✓ TOTP verification successful!</strong></p>
                  <p><strong>Final Step:</strong> Force password reset for the user:</p>
                  <div className="final-step-buttons">
                    <button 
                      onClick={handleForcePasswordResetFromTOTP}
                      className="force-reset-btn"
                    >
                      Force Password Reset
                    </button>
                    <button onClick={handleCancelTOTPReset} className="cancel-btn">
                      Cancel
                    </button>
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      )}

      <div className="users-section">
        <h2>Users ({users.length})</h2>
        {users.length === 0 ? (
          <p>No users found</p>
        ) : (
          <table>
            <thead>
              <tr>
                <th>Username (Email)</th>
                <th>Status</th>
                <th>Email Verified</th>
                {userPoolMFAConfig && userPoolMFAConfig.mfaConfiguration === 'OPTIONAL' && (
                  <th>MFA Status</th>
                )}
                <th>Company</th>
                <th>Name</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {users.map((user) => (
                <tr key={user.Username}>
                  <td>{user.Attributes?.find(attr => attr.Name === 'email')?.Value || user.Username}</td>
                  <td>{user.UserStatus}</td>
                  <td>
                    {user.Attributes?.find(attr => attr.Name === 'email_verified')?.Value === 'true' ? 'Yes' : 'No'}
                  </td>
                  {userPoolMFAConfig && userPoolMFAConfig.mfaConfiguration === 'OPTIONAL' && (
                    <td>
                      <span className={`mfa-status ${user.mfaStatus?.mfaActive ? 'mfa-active' : 'mfa-inactive'}`}>
                        {user.mfaStatus?.mfaActive ? 'MFA Active' : 'MFA Inactive'}
                      </span>
                    </td>
                  )}
                  <td>
                    {user.Attributes?.find(attr => attr.Name === 'custom:company')?.Value || 'N/A'}
                  </td>
                  <td>
                    {user.Attributes?.find(attr => attr.Name === 'name')?.Value || 'N/A'}
                  </td>
                  <td>
                    <div className="action-buttons">
                      <button
                        onClick={() => handleSetPermanentPassword(user.Username, user.Attributes?.find(attr => attr.Name === 'email')?.Value || user.Username)}
                        className="btn btn-primary btn-sm"
                        title="Set Permanent Password"
                      >
                        Set Password
                      </button>
                      <button
                        onClick={() => handleForcePasswordReset(user.Username, user.Attributes?.find(attr => attr.Name === 'email')?.Value || user.Username)}
                        className="btn btn-warning btn-sm"
                        title="Force Password Reset"
                      >
                        Reset Password
                      </button>
                      <button
                        onClick={() => handleResetTOTP(user.Username, user.Attributes?.find(attr => attr.Name === 'email')?.Value || user.Username)}
                        className="btn btn-info btn-sm"
                        title="Reset MFA"
                      >
                        Reset MFA
                      </button>
                      <button
                        onClick={() => handleEnableMFA(user.Username, user.Attributes?.find(attr => attr.Name === 'email')?.Value || user.Username)}
                        className="btn btn-success btn-sm"
                        title="Enable MFA"
                      >
                        Enable MFA
                      </button>
                      <button
                        onClick={() => handleDisableMFA(user.Username, user.Attributes?.find(attr => attr.Name === 'email')?.Value || user.Username)}
                        className="btn btn-secondary btn-sm"
                        title="Disable MFA"
                      >
                        Disable MFA
                      </button>
                      <button
                        onClick={() => handleDeleteUser(user.Username, user.Attributes?.find(attr => attr.Name === 'email')?.Value || user.Username)}
                        className="btn btn-danger btn-sm"
                        title="Delete User"
                      >
                        Delete
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}

export default AdminPage;